
#App {
  margin-top: 60px;
  color: #2c3e50;
  text-align: center;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#App .github {
    display: inline-block;
    margin-top: 30px;
    color: #42b983;
    font-size: 20px;
}

#Login .form {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
#Login .form-username, #Login .form-password, #Login .form-submit {
    margin: 5px;
    padding: 10px 15px;
    outline: none;
    border: 1px solid #dfdfdf;
    border-radius: 5px;
    font-size: 16px;
}
#Login .form-username::-webkit-input-placeholder, #Login .form-password::-webkit-input-placeholder, #Login .form-submit::-webkit-input-placeholder {
      color: #bebebe;
}
#Login .form-username::-ms-input-placeholder, #Login .form-password::-ms-input-placeholder, #Login .form-submit::-ms-input-placeholder {
      color: #bebebe;
}
#Login .form-username::placeholder, #Login .form-password::placeholder, #Login .form-submit::placeholder {
      color: #bebebe;
}
#Login .form-username:focus, #Login .form-password:focus, #Login .form-submit:focus {
      border: 1px solid #42b983;
}
#Login .form-submit {
    border: 1px solid #42b983;
    color: #42b983;
    -webkit-transition: 0.2s;
    transition: 0.2s;
}
#Login .form-submit:hover {
      background-color: #42b983;
      color: #ffffff;
}
#Login .form-submit:active {
      -webkit-box-shadow: 1px 1px 1px 1px #36a170 inset;
              box-shadow: 1px 1px 1px 1px #36a170 inset;
}

button {
  margin: 5px;
  padding: 10px 15px;
  outline: none;
  border: 1px solid #42b983;
  border-radius: 5px;
  color: #42b983;
  font-size: 16px;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}
button:hover {
    background-color: #42b983;
    color: #ffffff;
}
button:active {
    -webkit-box-shadow: 1px 1px 1px 1px #36a170 inset;
            box-shadow: 1px 1px 1px 1px #36a170 inset;
}
